<template>
  <div class="dialogue">
    <header id="wx-header">
      <div class="other">
        <router-link
          :to="{path:'/wechat/dialogue/dialogue-info',query: {top:$route.query.top,roomId:$route.query.roomId,msgInfo:JSON.stringify($store.state.chatlog)}}"
          tag="span"
          class="iconfont icon-chat-group"
          v-show="group_num&&group_num!=1"
        ></router-link>
        <router-link
          :to="{path:'/wechat/dialogue/dialogue-detail',query: { userId: userId,roomId:roomId,top:this.$route.query.top}}"
          tag="span"
          class="iconfont icon-chat-friends"
          v-show="group_num==1"
        ></router-link>
      </div>
      <div class="center">
        <router-link to="/" tag="div" class="iconfont icon-return-arrow">
          <span>消息</span>
        </router-link>
        <span>{{pageName}}</span>
        <span class="parentheses" v-show="group_num&&group_num!=1">{{group_num}}</span>
      </div>
    </header>

    <!-- 内容部分 -->
    <section
      class="dialogue-section clearfix"
      v-on:click="MenuOutsideClick"
      ref="dialogue"
      :style="contentStyleObj"
    >
      <!-- 下拉刷新 -->
      <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
        <div class="row clearfix" v-for="(item,index) in this.$store.state.chatlog.msg ">
          <!-- 时间 -->
          <div
            class="time"
            v-if="(zerotime-item.time<0&&item.time-item.beforTime>=60*1000)"
          >{{item.time |fmtDate(' hh:mm')}}</div>
          <div
            class="time"
            v-if="(zerotime-item.time>0&&zerotime-item.time<24*60*60*1000&&item.time-item.beforTime>=60*1000)"
          >{{item.time |fmtDate('昨天 hh:mm')}}</div>
          <div
            class="time"
            v-if="(zerotime-item.time>24*60*60*1000&&item.time-item.beforTime>=60*1000)"
          >{{item.time |fmtDate('yyyy-MM-dd hh:mm')}}</div>

          <img
            ref="head"
            :src="'/api/file/showImg?fileUrl='+item.user.headerUrl"
            class="header"
            :class="$store.state.chatlog.ownerId==item.user.userId?'right':''"
          />
          <!-- 渲染文本 -->
          <p
            v-if="item.msgType=='text'"
            :class="$store.state.chatlog.ownerId==item.user.userId?'righttext':''"
            class="text"
            v-more
            v-html="item.msg"
          ></p>
          <!-- >{{item.msg}}</p> -->
          <!-- 渲染图片 -->
          <img
            @click="preview(item.msg)"
            v-if="item.msgType == 'img'"
            :class="$store.state.chatlog.ownerId==item.user.userId?'righttext':''"
            class="text"
            :src="'/api/file/showImg?fileUrl='+item.msg"
            v-more
          />
          <span
            style="margin:8px;width:20px;height:20px;color:white;text-align:center;background:red;display:inline-block;border-radius:50%"
            v-if="flag"
          >!</span>
          <!-- <img :src="item.headerUrl" class="header" v-if="item.name!='老三'" /> -->
        </div>

        <span class="msg-more" id="msg-more">
          <ul>
            <li>复制</li>
            <li>转发</li>
            <li>收藏</li>
            <li @click="delChatData">删除</li>
          </ul>
        </span>
      </van-pull-refresh>
    </section>

    <footer class="dialogue-footer" ref="footer">
      <div class="component-dialogue-bar-person">
        <span
          class="iconfont icon-dialogue-jianpan"
          v-show="!currentChatWay"
          v-on:click="currentChatWay=true"
        ></span>
        <span
          class="iconfont icon-dialogue-voice"
          v-show="currentChatWay"
          v-on:click="currentChatWay=false"
        ></span>
        <div class="chat-way" v-show="!currentChatWay">
          <div class="chat-say" v-press>
            <span class="one">按住 说话</span>
            <span class="two">松开 结束</span>
          </div>
        </div>
        <div class="chat-way" v-show="currentChatWay">
          <input
            class="chat-txt"
            type="text"
            v-on:focus="focusIpt"
            v-on:blur="blurIpt"
            v-model="sendContent"
          />
        </div>
        <span class="sendStyle" @click="send" v-show="isShow">发送</span>
        <span class="expression iconfont icon-dialogue-smile" @click="smile">
          <!-- 表情 -->
        </span>
        <span class="more iconfont icon-dialogue-jia" @click="more" v-show="!isShow">
          <!-- + -->
        </span>
        <div class="recording" style="display: none;" id="recording">
          <div class="recording-voice" style="display: none;" id="recording-voice">
            <div class="voice-inner">
              <div class="voice-icon"></div>
              <div class="voice-volume">
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
                <span></span>
              </div>
            </div>
            <p>手指上划,取消发送</p>
          </div>
          <div class="recording-cancel" style="display: none;">
            <div class="cancel-inner"></div>
            <p>松开手指,取消发送</p>
          </div>
        </div>
      </div>
      <div class="add" ref="msgMore" style="display:none;">
        <van-row>
          <van-col span="6">
            <div style="padding-top:0.1px;">
              <!-- :before-read="beforeRead" -->
              <van-uploader accept="image/png, image/jpeg, image/jpg" :after-read="afterRead">
                <div class="bg-border">
                  <van-icon name="photo" size="25px" class="centerStyle" />
                </div>
              </van-uploader>
              <div>相册</div>
            </div>
          </van-col>
          <van-col span="6">
            <div>
              <van-uploader capture="camera" :after-read="afterRead">
                <div class="bg-border">
                  <van-icon name="photograph" size="25px" class="centerStyle" />
                </div>
              </van-uploader>
              <div>拍摄</div>
            </div>
          </van-col>
          <van-col span="6">
            <div>
              <div class="bg-border">
                <van-icon name="photo" size="25px" class="centerStyle" />
              </div>
              <div>相册</div>
            </div>
          </van-col>
          <van-col span="6">
            <div>
              <div class="bg-border">
                <van-icon name="photograph" size="25px" class="centerStyle" />
              </div>
              <div>拍摄</div>
            </div>
          </van-col>
        </van-row>
      </div>

      <!-- 表情 -->
      <div class="smile" ref="smileMore" style="display:none;overflow:auto">
        <!-- <img
          v-for="(item,index) in imgList"
          :key="index"
          :src="item.imgurl"
          @click="expression(item.expName,item.expUrl)"
        />-->
        <!-- <van-grid :column-num="8" :gutter="5" :square="true">
          <van-grid-item v-for="(item,index) in $store.state.sysExp" :key="index">
            <van-image
              :src="'/api/file/showImg?fileUrl='+item.expUrl"
              @click="expression(item.expName,item.expUrl)"
            />
          </van-grid-item>
        </van-grid>-->
        <van-grid :column-num="8" :gutter="5" :square="true">
          <van-grid-item v-for="(item,index) in imgList" :key="index">
            <van-image :src="item.imgurl" @click="expression(item.imgurl)" />
          </van-grid-item>
        </van-grid>
      </div>
    </footer>
  </div>
</template>
<script>
import qs from "qs";
import { Dialog, Toast, Uploader, ImagePreview, Grid, GridItem } from "vant";
import fmtDate from "../../filters";
import { getMessage, send } from "../../socket/socket";
export default {
  data() {
    return {
      isLoading: false, //是否在加载中
      userId: "", //本用户id
      roomId: "",
      contentStyleObj: {
        paddingBottom: ""
      },
      isShow: false,
      //0点时间
      zerotime: new Date(new Date().toLocaleDateString()).getTime(),
      //时间标准
      timeflag: 0,
      nowTime: Date.parse(new Date()),
      msgInfo: [],
      userInfo: "",
      socket: "",
      flag: false,
      sendContent: "",
      // pageName: this.$route.query.name,
      pageName: "", //聊天框名称
      group_num: "", //聊天人员数量
      currentChatWay: true, //ture为键盘打字 false为语音输入
      timer: null
      // sayActive: false // false 键盘打字 true 语音输入
    };
  },
  //进入聊天室的时候发送标识给后端
  beforeRouteEnter(to, from, next) {
    // console.log(to);
    next(vm => {
      vm.$store.commit("setPageName", vm.$route.query.name);
      if (to.path == "/wechat/dialogue") {
        let params = {
          from: vm.userInfo.userId,
          roomId: to.query.roomId,
          dataGroup: "1",
          dataType: "8"
        };
        send(params);
      }
      vm.$store.state.msgList.baseMsg.map(item => {
        if (item.roomId === to.query.roomId) {
          item.newMsgCount = 0;
        }
      });
    });
  },
  //离开聊天室的时候发送标识给后端
  beforeRouteLeave(to, from, next) {
    if (from.path == "/wechat/dialogue") {
      let params = {
        from: this.userInfo.userId,
        roomId: from.query.roomId,
        dataGroup: "1",
        dataType: "9"
      };
      send(params);
    }
    next();
  },
  directives: {
    press: {
      inserted(element, binding) {
        var recording = document.querySelector(".recording"),
          recordingVoice = document.querySelector(".recording-voice"),
          recordingCancel = document.querySelector(".recording-cancel"),
          startTx,
          startTy;

        element.addEventListener(
          "touchstart",
          function(e) {
            // 用bind时，vue还没插入到dom,故dom获取为 undefine，用 inserted 代替 bind,也可以开个0秒的定时器
            element.className = "chat-say say-active";
            recording.style.display = recordingVoice.style.display = "block";
            // console.log('start')
            var touches = e.touches[0];
            startTx = touches.clientX;
            startTy = touches.clientY;
            e.preventDefault();
          },
          false
        );
        element.addEventListener(
          "touchend",
          function(e) {
            /*var touches = e.changedTouches[0];
                        var distanceY = startTy - touches.clientY;
                        if (distanceY > 50) {
                            console.log("取消发送信息");
                        }else{
                            console.log("发送信息");
                        }*/

            element.className = "chat-say";
            recordingCancel.style.display = recording.style.display = recordingVoice.style.display =
              "none";
            // console.log('end')
            e.preventDefault();
          },
          false
        );
        element.addEventListener(
          "touchmove",
          function(e) {
            var touches = e.changedTouches[0],
              endTx = touches.clientX,
              endTy = touches.clientY,
              distanceX = startTx - endTx,
              distanceY = startTy - endTy;

            if (distanceY > 50) {
              element.className = "chat-say";
              recordingVoice.style.display = "none";
              recordingCancel.style.display = "block";
            } else {
              element.className = "chat-say say-active";
              recordingVoice.style.display = "block";
              recordingCancel.style.display = "none";
            }
            // 阻断事件冒泡 防止页面被一同向上滑动
            e.preventDefault();
          },
          false
        );
      }
    },
    more: {
      bind(element, binding) {
        var startTx, startTy;
        element.addEventListener(
          "touchstart",
          function(e) {
            console.log(e.target);
            var msgMore = document.getElementById("msg-more"),
              touches = e.touches[0];
            startTx = touches.clientX;
            startTy = touches.clientY;

            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
              // 控制菜单的位置
              msgMore.style.left =
                (startTx - 18 > 180 ? 180 : startTx - 18) + "px";
              msgMore.style.top = element.offsetTop - 33 + "px";
              msgMore.style.display = "block";
              element.style.backgroundColor = "#e5e5e5";
            }, 500);
          },
          false
        );
        element.addEventListener(
          "touchmove",
          function(e) {
            var touches = e.changedTouches[0],
              disY = touches.clientY;
            if (Math.abs(disY - startTy) > 10) {
              clearTimeout(this.timer);
            }
          },
          false
        );
        element.addEventListener(
          "touchend",
          function(e) {
            clearTimeout(this.timer);
          },
          false
        );
      }
    }
  },
  mounted() {
    this.userId = this.$route.query.userId;
    this.roomId = this.$route.query.roomId;
    this.paddingBottom();
    this.userInfo = JSON.parse(window.localStorage.getItem("userInfo"));
    this.getChat();
    this.scrollBottom(); //新消息置底
  },
  // beforeUpdate() {
  //   if (this.$route.query.userId == this.$store.state.toastMsg.user.userId) {
  //     this.$store.state.toastMsg.msg = "";
  //   }
  // },
  methods: {
    scrollBottom() {
      //聊天定位到底部
      this.$nextTick(() => {
        var div = this.$refs.dialogue;
        //设置滚动条到最底部
        div.scrollTop = div.scrollHeight;
      });
    },
    //表情匹配
    expmatch(val) {
      var res = val.split(/(\[[^[\]]+\])/);
      // console.log(res);
      let newArr = [];
      // let lastArr = [];
      res.map(item => {
        item = item.replace(/\[|]/g, "");
        newArr.push(item);
      });
      newArr.forEach((item, index) => {
        this.$store.state.sysExp.map(val => {
          if (item == val.expName) {
            newArr[index] =
              "<img src='/api/file/showImg?fileUrl=" + val.expUrl + "'/>";
          }
        });
      });
      let obj = newArr.join("");
      return obj;
    },
    //选择表情
    expression(name, url) {
      // console.log(name, url);
      this.sendContent += "[表情]";
    },
    //预览图片
    preview(images) {
      ImagePreview({
        images: ["/api/file/showImg?fileUrl=" + images]
      });
    },
    //下拉刷新
    onRefresh() {
      let params = {
        roomId: this.$route.query.roomId,
        lastMsgId: this.$store.state.chatlog.msg[0].id,
        limit: 10
      };
      this.$http.get("/api/chat/findChatData", { params: params }).then(res => {
        if (res.data.code == 200) {
          // console.log(res.data.data);
          // this.$toast("刷新成功");
          this.isLoading = false;
          this.$store.commit("pullRefresh", res.data.data.msg);
          if (this.$store.state.chatlog.msg.length) {
            this.$store.state.chatlog.msg[0].beforTime =
              this.$store.state.chatlog.msg[0].time - 60 * 1000;
            for (var i = 0; i < this.$store.state.chatlog.msg.length - 1; i++) {
              this.$store.state.chatlog.msg[
                i + 1
              ].beforTime = this.$store.state.chatlog.msg[i].time;
            }
          } else {
            for (var i = 0; i < this.$store.state.chatlog.msg.length - 1; i++) {
              this.$store.state.chatlog.msg[
                i + 1
              ].beforTime = this.$store.state.chatlog.msg[i].time;
            }
          }
        }
      });
    },
    //图片读取后函数
    afterRead(res) {
      let formData = new FormData();
      formData.append("file", res.file);

      this.$http
        .post("/api/file/upload/chat", formData, {
          headers: {
            //添加请求头
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          //发送成功走通道
          if (res.data.code == 200) {
            console.log(res);
            //通道发送
            if (this.$route.query.roomType == "friend") {
              let params = {
                fileOriName: res.data.data[0].fileOriName,
                fileUrl: res.data.data[0].fileUrl,
                fileSize: res.data.data[0].fileSize,
                fileName: res.data.data[0].fileName,
                fileSuffix: res.data.data[0].fileSuffix,
                from: this.userInfo.userId,
                to: this.$route.query.userId,
                roomId: this.$route.query.roomId,
                msgType: this.$global.imgType,
                msg: res.data.data[0].fileUrl,
                dataGroup: "1",
                dataType: "2"
              };
              send(params);
            } else if (this.$route.query.roomType == "group") {
              let params = {
                fileOriName: res.data.data[0].fileOriName,
                fileUrl: res.data.data[0].fileUrl,
                fileSize: res.data.data[0].fileSize,
                fileName: res.data.data[0].fileName,
                fileSuffix: res.data.data[0].fileSuffix,
                from: this.userInfo.userId,
                to: this.$route.query.userId,
                roomId: this.$route.query.roomId,
                msgType: this.$global.imgType,
                msg: res.data.data[0].fileUrl,
                dataGroup: "1",
                dataType: "4"
              };
              send(params);
            }

            //本地存储
            let obj = {
              msg: res.data.data[0].fileUrl,
              time: this.nowTime,
              ownerId: this.userInfo.userId,
              msgType: this.$global.imgType,
              user: {
                nickname: this.userInfo.nickname,
                headerUrl: this.userInfo.headerUrl,
                userId: this.userInfo.userId
              }
            };
            this.$store.commit("sendChat", obj);
          }
        });
    },
    paddingBottom() {
      //聊天内容动态设置padding
      this.contentStyleObj.paddingBottom =
        this.$refs.footer.offsetHeight + 10 + "px";
    },
    delChatData(e) {
      // console.log(this.msgInfo.msg);
      let params = {
        roomId: this.$route.query.roomId
        // msgIds:
      };
      // this.$http.post("/api/chat/delChatData",qs.stringify(params));
    },
    detail() {
      this.$router.push({
        path: "/wechat/dialogue/dialogue-detail",
        query: { msgInfo: this.msgInfo }
      });
    },
    //获取历史记录
    getChat() {
      let params = {
        roomId: this.$route.query.roomId,
        lastMsgId: null,
        limit: 10
      };
      this.findChatData(params);
    },
    findChatData(params) {
      this.$http.get("/api/chat/findChatData", { params: params }).then(res => {
        console.log(res.data.data);
        if (res.data.code == 200) {
          // this.$toast("刷新成功");
          // this.isLoading = false;
          this.pageName =
            res.data.data.roomName || res.data.data.user[0].nickname;
          this.group_num = res.data.data.user.length;
          this.$store.commit("saveLog", res.data.data);
          if (this.$store.state.chatlog.msg.length) {
            this.$store.state.chatlog.msg[0].beforTime =
              this.$store.state.chatlog.msg[0].time - 60 * 1000;
            for (var i = 0; i < this.$store.state.chatlog.msg.length - 1; i++) {
              this.$store.state.chatlog.msg[
                i + 1
              ].beforTime = this.$store.state.chatlog.msg[i].time;
            }
          } else {
            for (var i = 0; i < this.$store.state.chatlog.msg.length - 1; i++) {
              this.$store.state.chatlog.msg[
                i + 1
              ].beforTime = this.$store.state.chatlog.msg[i].time;
            }
          }

          // this.msgInfo = this.$store.state.chatlog;
          console.log(this.$store.state.chatlog);
        }
      });
    },
    //发送消息
    send() {
      //不能输入空消息，包括空格
      //console.log(this.$store.state.chatlog.msg[this.$store.state.chatlog.msg.length-1].time)
      let beforeTime = null;
      if (this.$store.state.chatlog.msg.length) {
        beforeTime = this.$store.state.chatlog.msg[
          this.$store.state.chatlog.msg.length - 1
        ].time;
      } else {
        beforeTime = Date.parse(new Date()) - 60 * 1000 - 1;
      }
      if (this.sendContent.replace(/(^\s*)|(\s*$)/g, "") !== "") {
        // console.log(this.expmatch(this.sendContent));

        let obj = {
          beforTime: beforeTime,
          msg: this.expmatch(this.sendContent),
          time: Date.parse(new Date()),
          ownerId: this.userInfo.userId,
          msgType: this.$global.textType,
          user: {
            nickname: this.userInfo.nickname,
            headerUrl: this.userInfo.headerUrl,
            userId: this.userInfo.userId
          }
        };
        // console.log(obj);
        this.$store.commit("sendChat", obj);
        if (this.$route.query.roomType == "friend") {
          //私聊发消息
          let params = {
            from: this.userInfo.userId,
            to: this.$route.query.userId,
            msg: this.expmatch(this.sendContent),
            msgType: this.$global.textType,
            roomId: this.$route.query.roomId,
            dataGroup: "1",
            dataType: "2"
          };
          send(params);
        } else if (this.$route.query.roomType == "group") {
          //群聊发消息
          let params = {
            from: this.userInfo.userId,
            msg: this.expmatch(this.sendContent),
            msgType: this.$global.textType,
            roomId: this.$route.query.roomId,
            msgType: this.$global.textType,
            dataGroup: "1",
            dataType: "4"
          };
          send(params);
        }

        this.sendContent = "";
      } else {
        Toast({
          type: "success",
          message: "不能发送空消息",
          icon: "success",
          duration: 1000
        });
      }
    },
    // 解决输入法被激活时 底部输入框被遮住问题
    //input获取焦点
    focusIpt() {
      this.timer = setInterval(function() {
        document.body.scrollTop = document.body.scrollHeight;
      }, 100);
    },
    //input失去焦点
    blurIpt() {
      clearInterval(this.timer);
    },
    smile() {
      this.$refs.smileMore.style.display = "block";
      this.$refs.msgMore.style.display = "none";
      this.$nextTick(() => {
        this.paddingBottom();
      });
    },
    //点击更多
    more() {
      this.$refs.smileMore.style.display = "none";
      this.$refs.msgMore.style.display = "block";
      this.$nextTick(() => {
        this.paddingBottom();
      });
    },
    // 点击空白区域，菜单被隐藏
    MenuOutsideClick(e) {
      // var container = document.querySelectorAll(".text"),
      //   msgMore = document.getElementById("msg-more");
      let container = document.querySelectorAll(".text");
      if (e.target.className === "text") {
      } else {
        this.$refs.msgMore.style.display = "none";
        this.$refs.smileMore.style.display = "none";
        container.forEach(item => (item.style.backgroundColor = "#fff"));
        this.paddingBottom();
      }
    }
  },
  computed: {
    inforList() {
      if (this.$store.state.chatlog.msg != undefined) {
        return this.$store.state.chatlog.msg.length;
      }
    },
    //本地表情地址
    imgList() {
      let arr = [];
      for (var i = 1; i < 63; i++) {
        if (i < 10) {
          i = "0" + i;
        }
        // console.log(i);
        arr.push({
          imgurl: require("../../assets/images/exp/" + i + ".png")
        });
      }
      return arr;
    }
  },
  watch: {
    //监听input框是否有值
    sendContent(val) {
      console.log(val.length);
      if (val.length != 0) {
        this.isShow = true;
      } else {
        this.isShow = false;
      }
    },
    inforList(val) {
      this.scrollBottom(); //新消息置底
    }
  }
};
</script>
<style scoped>
@import "../../assets/css/dialogue.css";
/* .van-pull-refresh {
  overflow: auto;
} */
.say-active {
  background: #c6c7ca;
}
.right {
  width: 35px;
  float: right !important;
  display: block;
}
.righttext {
  float: right !important;
  background: #b0e46e !important;
  padding: 8px;
  box-sizing: border-box;
  margin-right: 10px !important;
  position: relative;
  border-radius: 4px;
  max-width: 80%;
  font-size: 14px;
}

.righttext:before {
  width: 0;
  height: 0;
  position: absolute;
  left: -12px;
  top: 11px;
  content: "";
  border: 6px solid transparent !important ;
}

.righttext::after {
  width: 0;
  height: 0;
  position: absolute;
  right: -12px !important;
  top: 11px;
  content: "";
  border: 6px solid transparent;
  border-left-color: #b0e46e !important;
}
.time {
  color: #b2b2b2;
  text-align: center;
  margin-bottom: 10px;
}

#wx-header {
  width: 100%;
  position: fixed;
}
.component-dialogue-bar-person {
  height: 60px;
  padding: 10px;
}
.sendStyle {
  width: 50px;
  height: 32px;
  text-align: center;
  line-height: 32px;
  margin-left: 15px;
  margin-top: 5px;
  color: white;
  background: #57bd6d;
  margin-right: 15px;
  border-radius: 3px;
}
.add {
  height: 150px;
  padding: 20px 0;
  text-align: center;
  background: #f2f3f5;
}
.smile {
  height: 200px;
  padding: 20px 0;
  text-align: center;
  background: #f2f3f5;
}
.bg-border {
  background: white;
  border-radius: 10px;
  width: 50px;
  height: 50px;
  margin-left: 50%;
  transform: translateX(-50%);
  margin-bottom: 5px;
}
.centerStyle {
  margin-top: 50%;
  transform: translateY(-50%);
}
</style>


